import { v4 as uuidv4 } from 'uuid'
import { saveToLocalStorage, getDeviceInfo, retryFailedEvents, getSDKInfo } from './utils'
import { AppInformation, Device, EventPayload, SDK } from './types'

interface InitOptions {
    apiKey?: string
    appName: string
    appVersion: string
    trackingUrl: string
    loyaltyUrl: string
    token: string
    debug?: boolean
}

class Tracker {
    private apiKey: string | null = null
    private appInfo: AppInformation | null = null
    private debug: boolean = false
    private deviceInfo: Device | null = null
    private eventQueue: EventPayload[] = []
    private properties = {}
    private sdkInfo: SDK | null = null
    private userId: string | null = null
    private trackingUrl: string | null = null
    private loyaltyUrl: string | null = null

    init(options: InitOptions): void {
        const {
            token,
            appName,
            appVersion,
            trackingUrl,
            loyaltyUrl,
            apiKey = 'oneloyalty-app',
            debug = false
        } = options
        if (!apiKey) {
            console.error('Tracking SDK: Missing apiKey.')
            return
        }

        if (!token || !trackingUrl || !loyaltyUrl) {
            console.error('Tracking SDK: Missing token or trackingUrl or loyaltyUrl.')
            return
        }

        if (!appName || !appVersion) {
            console.error('Tracking SDK: Missing appName or appVersion.')
            return
        }

        this.apiKey = apiKey
        this.appInfo = {
            name: appName,
            version: appVersion
        }
        this.trackingUrl = trackingUrl
        this.loyaltyUrl = loyaltyUrl
        this.debug = debug
        this.deviceInfo = getDeviceInfo('')
        this.properties = { userAgent: navigator.userAgent }
        this.sdkInfo = getSDKInfo()

        this.getMe(token)

        if (this.debug) console.log('Tracking SDK initialized:', { token, trackingUrl })
        // retryFailedEvents(this.sendEvent.bind(this))
    }

    trackEvent(eventName: string, data: Record<string, any> = {}): void {
        if (!this.userId || !this.trackingUrl || !this.appInfo || !this.deviceInfo || !this.sdkInfo) {
            console.error('Tracking SDK: SDK not initialized.')
            return
        }

        const payload: EventPayload = {
            id: uuidv4(),
            specVersion: this.sdkInfo.version,
            source: `${this.appInfo.name}-${this.sdkInfo.name}`,
            type: 'EVENT',
            data: {
                deviceId: this.deviceInfo?.id,
                eventName,
                context: {
                    appInformation: this.appInfo,
                    sdk: this.sdkInfo,
                    device: this.deviceInfo
                },
                properties: { ...this.properties, userId: this.userId, ...data },
                timeRecord: Date.now()
            }
        }

        this.eventQueue.push(payload)
        this.sendEvent(payload)
    }

    private getMe(token: string): void {
        if (!this.apiKey || !this.loyaltyUrl || !token) return

        fetch(`${this.loyaltyUrl}/api/v1/users/id`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`,
                'X-Client-Id': `${this.apiKey}`
            }
        })
            .then(response => {
                if (!response.ok) throw new Error('Network response was not ok')
                return response.json()
            })
            .then(data => {
                if (this.debug) console.log('Parse token success:', data)
                this.userId = data?.data?.userId
            })
            .catch(error => {
                console.error('Parse token failed:', error)
            })
    }

    private sendEvent(payload: EventPayload): void {
        if (!this.apiKey || !this.userId || !this.trackingUrl) return

        fetch(`${this.trackingUrl}/publish/api/v1/user-events`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'X-Client-Id': `${this.apiKey}`
            },
            body: JSON.stringify({
                limit: 1,
                records: [payload]
            })
        })
            .then(response => {
                if (!response.ok) throw new Error('Network response was not ok')
                return response.json()
            })
            .then(data => {
                if (this.debug) console.log('Event tracked:', data)
                this.eventQueue = this.eventQueue.filter(e => e !== payload)
            })
            .catch(error => {
                console.error('Tracking SDK: Error tracking event:', error)
                saveToLocalStorage(payload)
            })
    }

    setUser(userId: string, data: Record<string, any> = {}): void {
        this.trackEvent('set_user', { userId, ...data })
    }
}

export default Tracker
